.canvasGraph
{
	border:2px solid #E68A5C;
	background-color:#FFFAF5;
	font-family: helvetica;
	text-align: center;
	vertical-align: center;
	width:1000px;
	height:650px;
	font-weight:lighter;
	position: absolute;
	top:315px;
	left:215px;


}

#topbar
{
	background-color:#A34719;
	float:top;
	float:left;
	display: inline;
	width:960px;
	height:40px;


}

.xbutton
{
	background-color:#B46A45;
	float:top;
	float:right;
	display: inline;
	height:40px;
	width:40px;
	cursor: pointer;

	color:#DAB4A2;
	font-size:40px;
	text-align: center;
	line-height: 30px;
}

.editbox
{
	text-align: left;
	float:left;
	position: relative;
	top:0px;
	left:30px;
}

.subeditbox
{
	position: relative;
	display:block;
}

.subeditbox div
{
	display: inline-block;
	position: relative;
	top: 20px;
}

#draw
{
	position: relative;
	top:-20px;
}

#teext
{
	position: absolute;
	top:200px;
}

#rotate
{
	position: relative;
	top:120px;
}

.shapes
{
	width:50px;
	height:50px;
	cursor: pointer;
}

.myDraw
{
	width:50px;
	height:50px;
	cursor: pointer;
}

.canvas
{

	border:2px solid black;
	z-index: 3;
	float:right;
	border-radius:5px;
	position:absolute;
	top:100px;
	right:50px;
}
 .canvasGraph p
 {
 	position: relative;
 	top:30px;
 }

#canvasImg
{
	position: absolute;
	top:-355px;
	left:417px;

}



